/* Scroll indicator */
.ad-portfolio-block .scroll-indicator {
  position: absolute;
  right: 2rem;
  bottom: 50px;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  z-index: 10;
  color: white;
  opacity: 0.7;
  transition: opacity 0.3s ease;
}

.ad-portfolio-block .scroll-indicator:hover {
  opacity: 1;
}

.ad-portfolio-block .scroll-text {
  writing-mode: vertical-rl;
  text-orientation: mixed;
  font-size: 0.9rem;
  font-weight: 500;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  transform: rotate(180deg);
}

.ad-portfolio-block .scroll-arrow {
  width: 2px;
  height: 40px;
  background: white;
  position: relative;
  border-radius: 1px;
}

.ad-portfolio-block .scroll-arrow::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 6px solid white;
}

@media (max-width: 768px) {
  .ad-portfolio-block .scroll-indicator {
    right: 1rem;
  }
  
  .ad-portfolio-block .scroll-text {
    font-size: 0.8rem;
  }
  
  .ad-portfolio-block .scroll-arrow {
    height: 30px;
  }
}
@media (max-width: 600px) {
	.ad-portfolio-block .ad-portfolio-block__modal-close-btn{
    top: 5rem;
  }
}

// Keyframe animations for button hover effects
@keyframes bounce {
  0%, 20%, 53%, 80%, 100% {
    transform: translate3d(0, 0, 0);
  }
  40%, 43% {
    transform: translate3d(0, -8px, 0);
  }
  70% {
    transform: translate3d(0, -4px, 0);
  }
  90% {
    transform: translate3d(0, -2px, 0);
  }
}

@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-2px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(2px);
  }
}